
import { injectGlobal } from 'styled-components'
injectGlobal`
    *{margin:0px;padding:0px;}
    ul, ol { list-style: none; }
    input{outline:0 none !important; blr:expression(this.onFocus=this.blur());}
    a{color:#000;text-decoration:none}

    @font-face {font-family: "iconfont";
        src: url('./font/iconfont.eot?t=1539827175204'); /* IE9*/
        src: url('./font/iconfont.eot?t=1539827175204#iefix') format('embedded-opentype'), /* IE6-IE8 */
        url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),
        url('./font/iconfont.ttf?t=1539827175204') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
        url('./font/iconfont.svg?t=1539827175204#iconfont') format('svg'); /* iOS 4.1- */
    }
    
    .iconfont {
        font-family:"iconfont" !important;
        font-size:16px;
        font-style:normal;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }

`
